    p2: '"–¶—è —Ä–æ—Å–ª–∏–Ω–∞ –ø—ñ–¥—Ç—Ä–∏–º—É—î —ó—Ö (—ñ–Ω–¥—ñ–∞–Ω—Ü—ñ–≤) —ñ –Ω–∞–¥–∞—î —ó–º —Å–∏–ª–∏ –≤ –±–æ—é, –ø–æ–∑–±–∞–≤–ª—è—é—á–∏ –≤—ñ–¥ —Å—Ç—Ä–∞—Ö—É, –≥–æ–ª–æ–¥—É —ñ —Å–ø—Ä–∞–≥–∏", ‚Äî –ø–∏—à–µ –≤—ñ–Ω.\n–ö—É–ª—å—Ç –∫–∞–∫—Ç—É—Å–∞ –ø–µ–π–æ—Ç–∞ —Ç–∞ —ñ–Ω—à–∏—Ö –≥–∞–ª—é—Ü–∏–Ω–æ–≥–µ–Ω—ñ–≤, –Ω–∞–ø—Ä–∏–∫–ª–∞–¥, –≥—Ä–∏–±—ñ–≤, –∑–∞—Ä–æ–¥–∏–≤—Å—è –≤ –¥–æ–∫–æ–ª—É–º–±–æ–≤—ñ–π –ê–º–µ—Ä–∏—Ü—ñ –≤ –≥–ª–∏–±–æ–∫—É –¥–∞–≤–Ω–∏–Ω—É. –£ –±–∞–≥–∞—Ç—å–æ—Ö –º—ñ—Å—Ü–µ–≤–æ—Å—Ç—è—Ö –¶–µ–Ω—Ç—Ä–∞–ª—å–Ω–æ—ó –ê–º–µ—Ä–∏–∫–∏ –∑—É—Å—Ç—Ä—ñ—á–∞—é—Ç—å—Å—è –∫–∞–º\'—è–Ω—ñ —Ñ—ñ–≥—É—Ä–∫–∏ –≥—Ä–∏–±—ñ–≤ –≤–∏—Å–æ—Ç–æ—é –¥–æ –º–µ—Ç—Ä–∞. –ó–∞ –æ–¥–Ω–∏–º —ñ–∑ –ø–µ—Ä–µ–∫–∞–∑—ñ–≤, —É —Ç—ñ –¥–∞–ª–µ–∫—ñ —á–∞—Å–∏ –∫—ñ–ª—å–∫–∞ —ñ–Ω–¥—ñ–∞–Ω—Å—å–∫–∏—Ö –ø–ª–µ–º–µ–Ω, –æ–±\'—î–¥–Ω–∞–≤—à–∏—Å—å, –≤—á–∏–Ω–∏–ª–∏ –∑–∞–∫–æ–ª–æ—Ç –ø—Ä–æ—Ç–∏ –≤–æ–π–æ–≤–Ω–∏—á–∏—Ö —ñ –∂–æ—Ä—Å—Ç–æ–∫–∏—Ö —Å—É—Å—ñ–¥—ñ–≤-–ø–æ–Ω–µ–≤–æ–ª–∏—Ç–µ–ª—ñ–≤. –ê–ª–µ —É –≤–∏—Ä—ñ—à–∞–ª—å–Ω—ñ–π –±–∏—Ç–≤—ñ –∑–∞–∫–æ–ª–æ—Ç–Ω–∏–∫–∏ –∑–∞–∑–Ω–∞–ª–∏ –ø–æ—Ä–∞–∑–∫–∏. –ü–æ–≤—Å—Ç–∞–Ω—Ü—ñ–≤ –≤–æ—Ä–æ–≥–∏ –∑–∞–≥–Ω–∞–ª–∏ –≤  –ø—É—Å—Ç–µ–ª—é, –¥–µ –Ω–∞ –Ω–∏—Ö —á–µ–∫–∞–ª–∞ –Ω–µ–≤—ñ–¥–≤–æ—Ä–æ—Ç–Ω–∞ —Å–º–µ—Ä—Ç—å –≤—ñ–¥ —Å–ø—Ä–∞–≥–∏. –ê–ª–µ –≤ –¥–æ—Ä–æ–∑—ñ —ó–º —Ç—Ä–∞–ø–∏–ª–∏—Å—è –º\'—è—Å–∏—Å—Ç—ñ –∫–∞–∫—Ç—É—Å–∏, –≤ —è–∫–∏—Ö, –∫—Ä—ñ–º –≤–æ–¥–∏, –≤–∏—è–≤–∏–ª–∞—Å—å —è–∫–∞—Å—å —Ä–µ—á–æ–≤–∏–Ω–∞, —â–æ –¥–æ–∑–≤–æ–ª–∏–ª–∞ –ª—é–¥—è–º –Ω–µ –ª–∏—à–µ –ø–µ—Ä–µ—Ç–Ω—É—Ç–∏ –ø—É—Å—Ç–µ–ª—é, –∞ –π –ø–æ—á—É—Ç–∏ –≥–æ–ª–æ—Å –±–æ–∂–µ—Å—Ç–≤–∞.\n–¶–µ –±—É–≤ –∫–∞–∫—Ç—É—Å –ø–µ–π–æ—Ç, —è–∫–∏–π —á–∞—Å—Ç–æ –∑—É—Å—Ç—Ä—ñ—á–∞—î—Ç—å—Å—è –Ω–∞ –ø—ñ–≤–Ω–æ—á—ñ –ú–µ–∫—Å–∏–∫–∏ —ñ –Ω–∞ –ø—ñ–≤–¥–µ–Ω–Ω–æ–º—É –∑–∞—Ö–æ–¥—ñ –°–®–ê.\n–í–µ—Ä—Ö–Ω—è —á–∞—Å—Ç–∏–Ω–∞ —Ä–æ—Å–ª–∏–Ω–∏ –≤–∏–∫–æ—Ä–∏—Å—Ç–æ–≤—É–≤–∞–ª–∞—Å—è —Ä—ñ–∑–Ω–∏–º–∏ —ñ–Ω–¥—ñ–∞–Ω—Å—å–∫–∏–º–∏ –ø–ª–µ–º–µ–Ω–∞–º–∏ –≤ —Ä–µ–ª—ñ–≥—ñ–π–Ω–∏—Ö —Ü–µ—Ä–µ–º–æ–Ω—ñ—è—Ö. –ö–∞–∫—Ç—É—Å —Ä–æ–∑—Ä—ñ–∑–∞–ª–∏ –Ω–∞ ¬´–º–µ—Å–∫–∞–ª—ñ–Ω–æ–≤—ñ —à–º–∞—Ç–æ—á–∫–∏¬ª, –Ω–∏–∂–Ω—é —á–∞—Å—Ç–∏–Ω—É —Ä–æ—Å–ª–∏–Ω–∏ –≤–∏–∫–æ—Ä–∏—Å—Ç–æ–≤—É–≤–∞–ª–∏ –≤ —ó–∂—É.\n–ë–∞–≥–∞—Ç–æ –ª—é–¥–µ–π –≤–∏–∫–æ—Ä–∏—Å—Ç–æ–≤—É–≤–∞–ª–∏ –º–µ—Å–∫–∞–ª—ñ–Ω –¥–ª—è —Å–≤–æ—ó—Ö –æ—Å–æ–±–∏—Å—Ç–∏—Ö —Ü—ñ–ª–µ–π.\n–ù–∞–ø—Ä–∏–∫–ª–∞–¥, —Ü–µ—Ä–∫–≤–∞ –ö–æ—Ä—ñ–Ω–Ω–∏—Ö –ê–º–µ—Ä–∏–∫–∞–Ω—Ü—ñ–≤ —â–µ –≤ 1918 —Ä–æ—Ü—ñ –æ—Ç—Ä–∏–º–∞–ª–∞ –≤ –û–∫–ª–∞—Ö–æ–º—ñ –¥–æ–∑–≤—ñ–ª –Ω–∞ –≤–∏–∫–æ—Ä–∏—Å—Ç–∞–Ω–Ω—è –ø–µ–π–æ—Ç–∞ —É —Å–≤–æ—ó—Ö —Ä–µ–ª—ñ–≥—ñ–π–Ω–∏—Ö —Ü–µ—Ä–µ–º–æ–Ω—ñ—è—Ö. –ü—ñ–∑–Ω—ñ—à–µ, –≤ 1990 —Ä–æ—Ü—ñ, —Ü–µ–π –¥–æ–∑–≤—ñ–ª –±—É–ª–æ —Å–∫–∞—Å–æ–≤–∞–Ω–æ –í–∏—â–∏–º –°—É–¥–æ–º –°–®–ê. –ó\'—è—Å—É–≤–∞–ª–æ—Å—è, —â–æ –∑–∞ —Å–≤–æ—î—é –ø—Ä–∏—Ä–æ–¥–æ—é –º–µ—Å–∫–∞–ª—ñ–Ω —î –≥–∞–ª—é—Ü–∏–Ω–æ–≥–µ–Ω–æ–º.',
    t3: '–ú–µ—Å–∫–∞–ª—ñ–Ω —Å—å–æ–≥–æ–¥–Ω—ñ',
    p3: '–°—É—á–∞—Å–Ω—ñ –¥–æ—Å–ª—ñ–¥–∂–µ–Ω–Ω—è –≤—Å—Ç–∞–Ω–æ–≤–∏–ª–∏, —â–æ –º–µ—Å–∫–∞–ª—ñ–Ω–æ–≤–µ –Ω–∞—Å—ñ–Ω–Ω—è –º—ñ—Å—Ç–∏—Ç—å –æ—Ç—Ä—É–π–Ω—É —Ä–µ—á–æ–≤–∏–Ω—É, –ø–æ–¥—ñ–±–Ω—É –¥–æ –Ω—ñ–∫–æ—Ç–∏–Ω—É –∑–∞ –º–µ—Ö–∞–Ω—ñ–∑–º–æ–º –≤–ø–ª–∏–≤—É –Ω–∞ –æ—Ä–≥–∞–Ω—ñ–∑–º.\n–°–∏–º–ø—Ç–æ–º–∏ –æ—Ç—Ä—É—î–Ω–Ω—è –∞–Ω–∞–ª–æ–≥—ñ—á–Ω—ñ –¥–æ –õ–°–î. –î—ñ—è –ø—Ä–æ—è–≤–ª—è—î—Ç—å—Å—è –ø—Ä–∏–±–ª–∏–∑–Ω–æ —á–µ—Ä–µ–∑ 2 –≥–æ–¥–∏–Ω–∏ —ñ —Ç—Ä–∏–≤–∞—î –±–ª–∏–∑—å–∫–æ 12 –≥–æ–¥–∏–Ω. –ó\'—è–≤–ª—è—é—Ç—å—Å—è –ø–æ—Ä—É—à–µ–Ω–Ω—è –∑–æ—Ä—É, –Ω—É–¥–æ—Ç–∞, —Ç—Ä–µ–º—Ç—ñ–Ω–Ω—è, –∫–æ–Ω–≤—É–ª—å—Å—ñ—ó, –≥–∞–ª—é—Ü–∏–Ω–∞—Ü—ñ—ó, —Ä–æ–∑—à–∏—Ä—é—é—Ç—å—Å—è –∑—ñ–Ω–∏—Ü—ñ; –º–æ–∂–ª–∏–≤–∞ —Å–º–µ—Ä—Ç—å –≤—ñ–¥ –ø–∞—Ä–∞–ª—ñ—á—É –¥–∏—Ö–∞–ª—å–Ω–æ–≥–æ —Ü–µ–Ω—Ç—Ä—É.',
