    t2: '–ù–∞—Å–ª—ñ–¥–∫–∏ –¥–ª—è –∑–¥–æ—Ä–æ–≤\'—è',
    p2: '–°—Ç–∏–º—É–ª—é—é—á—ñ –≤–ª–∞—Å—Ç–∏–≤–æ—Å—Ç—ñ –µ—Ñ–µ–¥—Ä–∏–Ω—É –±—É–ª–∏ –≤—ñ–¥–æ–º—ñ –∫–∏—Ç–∞–π—Ü—è–º —â–µ 5 —Ç–∏—Å—è—á —Ä–æ–∫—ñ–≤ —Ç–æ–º—É.\n–î—ñ—è —Ü—ñ—î—ó —Ä–µ—á–æ–≤–∏–Ω–∏ –≤–∏–∫–ª–∏–∫–∞—î –≤—ñ–¥—á—É—Ç—Ç—è, –Ω—ñ–±–∏ –ø–æ —Ç—ñ–ª—É "–±—ñ–≥–∞—é—Ç—å –º—É—Ä–∞—à–∫–∏", –≤—ñ–¥—á—É–≤–∞—î—Ç—å—Å—è —Å—É—Ö—ñ—Å—Ç—å —É —Ä–æ—Ç—ñ, —á–∞—Å—Ç—ñ—à–∞—î –ø—É–ª—å—Å. –ù–∞—Å—Ç—Ä—ñ–π –ø—ñ–¥–≤–∏—â—É—î—Ç—å—Å—è, –≤—Ç—Ä–∞—á–∞—î—Ç—å—Å—è –≤—ñ–¥—á—É—Ç—Ç—è —á–∞—Å—É, —Ö–æ—á–µ—Ç—å—Å—è –±–∞–≥–∞—Ç–æ –≥–æ–≤–æ—Ä–∏—Ç–∏, —Å–ø—ñ–ª–∫—É–≤–∞—Ç–∏—Å—è –∑ –ª—é–¥—å–º–∏, –≤–µ—Å–µ–ª–∏—Ç–∏—Å—è, —Ä—É—Ö–∞—Ç–∏—Å—è; –≤—Å–µ –¥–æ–≤–∫–æ–ª–∞ –Ω—ñ–±–∏ —É –ø—Ä–∏—î–º–Ω–∏—Ö —Ä–∞–π–¥—É–∂–Ω–∏—Ö —Ç–æ–Ω–∞—Ö. –ß–µ—Ä–µ–∑ –∫–æ—Ä–æ—Ç–∫–∏–π —á–∞—Å –ø—ñ—Å–ª—è –æ—Ç—Ä—É—î–Ω–Ω—è –µ—Ñ–µ–¥—Ä–∏–Ω–æ–º –Ω–∞—Å—Ç–∞—î —Ä—ñ–∑–∫–µ –ø–æ–≥—ñ—Ä—à–µ–Ω–Ω—è —Å–∞–º–æ–ø–æ—á—É—Ç—Ç—è —ñ –Ω–∞—Å—Ç—Ä–æ—é, –ª—é–¥–∏–Ω–∞ –≤—ñ–¥—á—É–≤–∞—î —Å–ª–∞–±–∫—ñ—Å—Ç—å, —Å—Ç–∞—î –¥—Ä–∞—Ç—ñ–≤–ª–∏–≤–æ—é, –æ–∑–ª–æ–±–ª–µ–Ω–æ—é, –º–æ–∂–µ –≤–ø–∞—Å—Ç–∏ –≤ —Å–∏–ª—å–Ω—É –¥–µ–ø—Ä–µ—Å—ñ—é. –ú–æ–∂—É—Ç—å –≤–∏–Ω–∏–∫–Ω—É—Ç–∏ —Å—É—ó—Ü–∏–¥–∞–ª—å–Ω—ñ —ñ–¥–µ—ó, —â–æ —à—Ç–æ–≤—Ö–∞—é—Ç—å –Ω–∞—Ä–∫–æ–º–∞–Ω–∞ –Ω–∞ —Å–∞–º–æ–≥—É–±—Å—Ç–≤–æ.',
