    p3: '–ù–∞—Ä–∫–æ—Ç–∏–∫ ‚Äú—Å—ñ–ª—å‚Äù –≤–∂–∏–≤–∞—é—Ç—å, –∫—É—Ä—è—á–∏, –≤–¥–∏—Ö–∞—é—á–∏ —á–µ—Ä–µ–∑ –Ω—ñ—Å –∞–±–æ —á–µ—Ä–µ–∑ —ñ–Ω\'—î–∫—Ü—ñ—é. –í–∏–∫–ª–∏–∫–∞—î —Ç—è–∂—á—ñ –Ω–∞—Å–ª—ñ–¥–∫–∏, –Ω—ñ–∂ –≥–µ—Ä–æ—ó–Ω. ‚Äú–°–æ–ª—ñ‚Äù –≤–±–∏–≤–∞—é—Ç—å –ª—é–¥–∏–Ω—É —à–≤–∏–¥–∫–æ ‚Äî –≤ —Å–µ—Ä–µ–¥–Ω—å–æ–º—É –∑–∞ 1 —Ä—ñ–∫.\n–î–µ—è–∫—ñ –Ω–∞—Å–ª—ñ–¥–∫–∏ –≤–∂–∏–≤–∞–Ω–Ω—è: –Ω–µ–∫–µ—Ä–æ–≤–∞–Ω–∏–π —Å—Ç—Ä–∞—Ö, –ø–∞—Ä–∞–Ω–æ—è, –≤—Ç—Ä–∞—Ç–∞ –ø–∞–º\'—è—Ç—ñ, –Ω–æ—Å–æ–≤—ñ –∫—Ä–æ–≤–æ—Ç–µ—á—ñ, —Å–ø–∞–∑–º–∏ —Å—É–¥–∏–Ω (—É –ª—é–¥–∏–Ω–∏ —Å–∏–Ω—ñ—é—Ç—å —Ä—É–∫–∏ —ñ –Ω–æ–≥–∏); —Ç–µ–º–ø–µ—Ä–∞—Ç—É—Ä–∞ 40-41¬∫, —Å—É–¥–æ–º–∏, –≤—ñ–¥—Å—É—Ç–Ω—ñ—Å—Ç—å –∞–ø–µ—Ç–∏—Ç—É, –±–æ–ª—ñ –≤ –≥—Ä—É–¥—è—Ö, –≥–æ—Ä–ª—ñ, –Ω–æ—Å—ñ, —É—Ç—Ä—É–¥–Ω–µ–Ω–µ –¥–∏—Ö–∞–Ω–Ω—è, —ñ–Ω—Ñ–∞—Ä–∫—Ç; –≤—ñ–¥–º–æ–≤–ª—è—é—Ç—å –ø–µ—á—ñ–Ω–∫–∞ —ñ –Ω–∏—Ä–∫–∏; —Ä–æ–∑–∫–ª–∞–¥–∞—é—Ç—å—Å—è —Ç–∫–∞–Ω–∏–Ω–∏ —Ç—ñ–ª–∞ —ñ –≤–Ω—É—Ç—Ä—ñ—à–Ω—ñ –æ—Ä–≥–∞–Ω–∏; –Ω–∞ —à–∫—ñ—Ä—ñ –∑\'—è–≤–ª—è—é—Ç—å—Å—è –≤–∏—Ä–∞–∑–∫–∏, —Å—Ç–∞—î—Ç—å—Å—è –∑–∞—Ä–∞–∂–µ–Ω–Ω—è –∫—Ä–æ–≤—ñ.\n–¢—è–∂–∫—ñ –π –Ω–µ–∑–≤–æ—Ä–æ—Ç–Ω—ñ –Ω–∞—Å–ª—ñ–¥–∫–∏ –¥–ª—è —Ñ—ñ–∑–∏—á–Ω–æ–≥–æ –∑–¥–æ—Ä–æ–≤\'—è —ñ –ø—Å–∏—Ö—ñ–∫–∏ –º–æ–∂—É—Ç—å –≤–∏–Ω–∏–∫–Ω—É—Ç–∏ –ù–ê–í–Ü–¢–¨ –ü–Ü–°–õ–Ø –ü–ï–†–®–û–ì–û –í–ñ–ò–í–ê–ù–ù–Ø!',
