    p1: '–ê–ª–∫–æ–≥–æ–ª—å - —Ü–µ –Ω–∞—Ä–∫–æ—Ç–∏–∫, —è–∫–∏–π –Ω–∞–ª–µ–∂–∏—Ç—å –¥–æ –≥—Ä—É–ø–∏ –¥–µ–ø—Ä–µ—Å–∞–Ω—Ç—ñ–≤, —Ç–æ–±—Ç–æ —î —Ç–∞–∫–∏–º, —è–∫–∏–π —É–ø–æ–≤—ñ–ª—å–Ω—é—î –∂–∏—Ç—Ç—î–≤–æ –≤–∞–∂–ª–∏–≤—ñ —Ñ—É–Ω–∫—Ü—ñ—ó: –º–æ–≤–ª–µ–Ω–Ω—è, –∫–æ–æ—Ä–¥–∏–Ω–∞—Ü—ñ—é —Ä—É—Ö—ñ–≤, —Å–ø—Ä–∏–π–Ω—è—Ç—Ç—è –æ—Ç–æ—á—É—é—á–æ—ó –¥—ñ–π—Å–Ω–æ—Å—Ç—ñ —Ç–∞ —ñ–Ω.\n–£ –ø–æ–±—É—Ç—ñ –ª—é–¥–∏ –∑–≤–∏–∫–ª–∏ –≤–∂–∏–≤–∞—Ç–∏ —Å–ª–æ–≤–æ ‚Äú–Ω–∞—Ä–∫–æ—Ç–∏–∫–∏‚Äù –ª–∏—à–µ —â–æ–¥–æ –∑–∞–±–æ—Ä–æ–Ω–µ–Ω–∏—Ö –Ω–∞—Ä–∫–æ—Ç–∏—á–Ω–∏—Ö —Ä–µ—á–æ–≤–∏–Ω (–º–∞—Ä–∏—Ö—É–∞–Ω–∞, –≥–µ—Ä–æ—ó–Ω, –∫–æ–∫–∞—ó–Ω —Ç–æ—â–æ), —Ç–æ–¥—ñ —è–∫ –∞–ª–∫–æ–≥–æ–ª—å —Ç—Ä–∞–¥–∏—Ü—ñ–π–Ω–æ –≤–≤–∞–∂–∞—é—Ç—å —á–∏–º–æ—Å—å –æ–∫—Ä–µ–º–∏–º, —Ö–æ—á–∞ –Ω–∞—Å–ø—Ä–∞–≤–¥—ñ –∑ –º–µ–¥–∏—á–Ω–æ—ó —Ç–æ—á–∫–∏ –µ—Ç–∞–Ω–æ–ª (–µ—Ç–∏–ª–æ–≤–∏–π —Å–ø–∏—Ä—Ç) —î –æ—Ç—Ä—É–π–Ω–æ—é —Ä–µ—á–æ–≤–∏–Ω–æ—é, –Ω–∞—Ä–∫–æ—Ç–∏–∫–æ–º. –Ü —Ç–æ–π —Ñ–∞–∫—Ç, —â–æ —Ü—è —Ä–µ—á–æ–≤–∏–Ω–∞ –¥–æ–∑–≤–æ–ª–µ–Ω–∞ –∑–∞–∫–æ–Ω–æ–¥–∞–≤—Å—Ç–≤–æ–º —É –Ω–∞—à—ñ–π –∫—Ä–∞—ó–Ω—ñ, –Ω–µ —Ä–æ–±–∏—Ç—å –∞–ª–∫–æ–≥–æ–ª—å –∫–æ—Ä–∏—Å–Ω–∏–º —á–∏ –±–µ–∑–ø–µ—á–Ω–∏–º. –¢–∞–∫ —Å–∞–º–æ, —è–∫ —ñ —ñ–Ω—à—ñ –Ω–∞—Ä–∫–æ—Ç–∏–∫–∏, –≤—ñ–Ω –≤–∏–∫–ª–∏–∫–∞—î –ø–µ–≤–Ω–∏–π —Å—Ç–∞–Ω –µ–π—Ñ–æ—Ä—ñ—ó, —Ñ—ñ–∑–∏—á–Ω—É —ñ –ø—Å–∏—Ö—ñ—á–Ω—É –∑–∞–ª–µ–∂–Ω—ñ—Å—Ç—å, –ø—Ä–∏–∑–≤–æ–¥–∏—Ç—å –¥–æ —Ç—è–∂–∫–∏—Ö –Ω–∞—Å–ª—ñ–¥–∫—ñ–≤ –¥–ª—è –∑–¥–æ—Ä–æ–≤\'—è —ñ —Å–º–µ—Ä—Ç—ñ.',
    t2: '–Ü—Å—Ç–æ—Ä—ñ—è –∞–ª–∫–æ–≥–æ–ª—é',
