    p11: '–û–¥–∏–Ω –∑ –º–æ–ª–æ–¥–∏—Ö –ª—é–¥–µ–π –Ω–µ —Ä–µ–∞–≥—É–≤–∞–≤ –Ω—ñ –Ω–∞ —è–∫—ñ –∑–≤–µ—Ä–Ω–µ–Ω—ñ –¥–æ –Ω—å–æ–≥–æ –ø–∏—Ç–∞–Ω–Ω—è. –Æ–Ω–∞–∫ –ø–æ—Å—Ç—ñ–π–Ω–æ –æ–∑–∏—Ä–∞–≤—Å—è, –Ω–∞ –æ–±–ª–∏—á—á—ñ –±–ª—É–∫–∞–ª–∞ –¥—É—Ä–Ω—É–≤–∞—Ç–æ-–ø–µ—Ä–µ–ª—è–∫–∞–Ω–∞ —É—Å–º—ñ—à–∫–∞. –Ñ–¥–∏–Ω–æ—é –π–æ–≥–æ –¥—ñ—î—é –±—É–ª–∏ –ø–æ—Å—Ç—ñ–π–Ω—ñ —Å–ø—Ä–æ–±–∏ –∑–Ω—è—Ç–∏ —à—Ç–∞–Ω–∏. –°–∞–Ω—ñ—Ç–∞—Ä–∏ –π–æ–≥–æ –æ–¥—è–≥–∞–ª–∏, –≤—ñ–Ω –Ω–µ —á–∏–Ω–∏–≤ –æ–ø–æ—Ä—É, –∞–ª–µ –ø–æ—Ç—ñ–º –∑–Ω–æ–≤—É —Ä–æ–∑—Å—Ç—ñ–±–∞–≤ —à—Ç–∞–Ω–∏ —ñ –æ–≥–æ–ª—é–≤–∞–≤—Å—è. –£ —Ç–∞–∫–æ–º—É —Å—Ç–∞–Ω—ñ –Ω–∞—Ä–∫–æ–º–∞–Ω–∏ –º–æ–∂—É—Ç—å –±–µ–∑–ø–µ—Ä–µ—Ä–≤–Ω–æ –ø–æ–≤—Ç–æ—Ä—é–≤–∞—Ç–∏ —è–∫—É—Å—å –∫–æ—Ä–æ—Ç–∫—É —Ñ—Ä–∞–∑—É. –û–¥–Ω–∞ –∑ –ø–∞—Ü—ñ—î–Ω—Ç–æ–∫ –Ω–∞—Ä–∫–æ–¥–∏—Å–ø–∞–Ω—Å–µ—Ä—É –∫—ñ–ª—å–∫–∞ –≥–æ–¥–∏–Ω –ø–æ—Å–ø—ñ–ª—å –ø–æ–≤—Ç–æ—Ä—é–≤–∞–ª–∞: ¬´–ê–Ω—å–∫–∞ ‚Äî –∑–∞—Ä–∞–∑–∞. –ê–Ω—å–∫–∞ ‚Äî –∑–∞—Ä–∞–∑–∞. –ê–Ω—å–∫–∞ ‚Äî –∑–∞—Ä–∞–∑–∞¬ª. –ö–æ–ª–∏ –¥—ñ–≤—á–∏–Ω–∞ –æ—Ç—è–º–∏–ª–∞—Å—è, —Ç–æ –Ω–µ –∑–º–æ–≥–ª–∞ –∑–≥–∞–¥–∞—Ç–∏, –Ω—ñ —Ö—Ç–æ —Ç–∞–∫–∞ –ê–Ω—å–∫–∞, –Ω—ñ —á–æ–º—É –≤–æ–Ω–∞ –±—É–ª–∞ –∑–∞—Ä–∞–∑–æ—é.',
