    p17: '–Ø–∫—â–æ –ø—ñ—Å–ª—è —Ä–æ–∑—Ç–∏–Ω—É –ø–æ–¥–∏–≤–∏—Ç–∏—Å—è –Ω–∞ –º–æ–∑–æ–∫ –ø–µ—Ä–≤—ñ—Ç–∏–Ω–æ–≤–æ–≥–æ –Ω–∞—Ä–∫–æ–º–∞–Ω–∞, —Ç–æ –≤—ñ–Ω –Ω–∞–≥–∞–¥—É–≤–∞—Ç–∏–º–µ –∑–∞—Å–æ—Ö–ª–µ, –∑–º–æ—Ä—â–µ–Ω–µ —è–±–ª—É–∫–æ. –¶–µ –ø–æ–≤\'—è–∑–∞–Ω–æ –∑ —Ç–∏–º, —â–æ –≤ —Ä–µ–∑—É–ª—å—Ç–∞—Ç—ñ –ø—Ä–∏–π–æ–º—É –ø–µ—Ä–≤—ñ—Ç–∏–Ω—É –≤–∏–Ω–∏–∫–∞—é—Ç—å —Å—Ç—ñ–π–∫—ñ –∑–º—ñ–Ω–∏ –Ω–µ—Ä–≤–æ–≤–æ—ó —Å–∏—Å—Ç–µ–º–∏. –¶–µ –≤—ñ–¥–±—É–≤–∞—î—Ç—å—Å—è —Ç–æ–º—É —â–æ –º–æ–∑–æ–∫ –ø–æ—Å—Ç—ñ–π–Ω–æ –∑–Ω–∞—Ö–æ–¥–∏—Ç—å—Å—è –≤ –ø–µ—Ä–µ–≤–∞–Ω—Ç–∞–∂–µ–Ω–æ–º—É —Å—Ç–∞–Ω—ñ. –ù–µ—Ä–≤–æ–≤—ñ –∫–ª—ñ—Ç–∏–Ω–∏ –ø–µ—Ä–µ–∑–±—É–¥–∂–µ–Ω—ñ —Å–∏–≥–Ω–∞–ª–∞–º–∏, —è–∫—ñ –Ω–∞–¥—Ö–æ–¥—è—Ç—å —ñ–∑ –∑–æ–≤–Ω—ñ—à–Ω—å–æ–≥–æ —Å–µ—Ä–µ–¥–æ–≤–∏—â–∞.\n–õ—é–¥–∏–Ω–∞, —è–∫–∞ –ø–µ—Ä–µ–±—É–≤–∞—î –≤ —Å—Ç–∞–Ω—ñ –ø–µ—Ä–≤—ñ—Ç–∏–Ω–æ–≤–æ–≥–æ —Å–ø\'—è–Ω—ñ–Ω–Ω—è, –≤—ñ–¥–∫–ª—é—á–µ–Ω–∞ –≤—ñ–¥ —ñ–º–ø—É–ª—å—Å—ñ–≤ –≤–Ω—É—Ç—Ä—ñ—à–Ω—å–æ–≥–æ —Å–≤—ñ—Ç—É, –ø–æ–Ω—è—Ç—å –º–æ—Ä–∞–ª—å–Ω–æ—ó —Å–∞–º–æ–æ—Ü—ñ–Ω–∫–∏ –π —Å–æ–≤—ñ—Å—Ç—ñ. –ù–∞—Ä–∫–æ–º–∞–Ω –Ω–µ –∑–¥–∞—Ç–Ω–∏–π –¥–∞—Ç–∏ –æ—Ü—ñ–Ω–∫—É –≤–ª–∞—Å–Ω—ñ–π –ø–æ–≤–µ–¥—ñ–Ω—Ü—ñ. –í—ñ–Ω —Å—Ç–∞—î –∞–±—Å–æ–ª—é—Ç–Ω–æ –∫–µ—Ä–æ–≤–∞–Ω–∏–º, –æ—Å–∫—ñ–ª—å–∫–∏ —É —Å—Ç–∞–Ω—ñ –ø–µ—Ä–µ–∑–±—É–¥–∂–µ–Ω–Ω—è –π–æ–º—É –Ω–∞–π–ª–µ–≥—à–µ –≤–∏–∫–æ–Ω—É–≤–∞—Ç–∏ —á—É–∂—ñ –≤–∫–∞–∑—ñ–≤–∫–∏. –ú–æ–ª–æ–¥–∞ –ª—é–¥–∏–Ω–∞ —É —Å—Ç–∞–Ω—ñ —Å–ø\'—è–Ω—ñ–Ω–Ω—è –ø—Å–∏—Ö–æ—Å—Ç–∏–º—É–ª—è—Ç–æ—Ä–∞–º–∏ –Ω–µ –º–æ–∂–µ –ø—Ä–∞–≤–∏–ª—å–Ω–æ –æ—Ü—ñ–Ω–∏—Ç–∏ —Å–∏—Ç—É–∞—Ü—ñ—é, –Ω–µ –∑–¥–∞—Ç–Ω–∞ –¥–æ –ø—Ä–∏–π–Ω—è—Ç—Ç—è —Å–∞–º–æ—Å—Ç—ñ–π–Ω–∏—Ö —Ä—ñ—à–µ–Ω—å.\n–¢–∞–∫–∏–π –Ω–∞–ø—ñ–≤–≥—ñ–ø–Ω–æ—Ç–∏—á–Ω–∏–π —Å—Ç–∞–Ω –∑–±–µ—Ä—ñ–≥–∞—î—Ç—å—Å—è –±–ª–∏–∑—å–∫–æ –¥–æ–±–∏ –ø—ñ—Å–ª—è –æ—Å—Ç–∞–Ω–Ω—å–æ–≥–æ —É–∫–æ–ª—É –ø–µ—Ä–≤—ñ—Ç–∏–Ω—É. –õ—é–¥–∏–Ω–∞ –ª–µ–≥–∫–æ –ø—ñ–¥–∫–æ—Ä—é—î—Ç—å—Å—è –±—É–¥—å-–∫–æ–º—É. –£ —Å—Ç–∞–Ω—ñ —Å–ø\'—è–Ω—ñ–Ω–Ω—è –Ω–∞—Ä–∫–æ–º–∞–Ω, –ø–æ —Å—É—Ç—ñ, —Ä–∞–± –≤–∏—Ä–æ–±–Ω–∏–∫–∞ –Ω–∞—Ä–∫–æ—Ç–∏–∫—É —ñ –¥–æ—Å–≤—ñ–¥—á–µ–Ω—ñ—à–∏—Ö ¬´—Ç–æ–≤–∞—Ä–∏—à—ñ–≤¬ª —É –∫–æ–º–ø–∞–Ω—ñ—ó.',
