const narcoticsReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_NARCOTIC: {
      return { visibility: true, partOfState: initialState[action.id], allNarcotics: initialState.allNarcotics };
    }
    default:
      return { ...state };
  }
};

export const setNarcoticAC = id => ({ type: SET_NARCOTIC, id });

export default narcoticsReducer;
