const faqPagereducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_FAQ: {
      return { visibility: true, partOfState: initialState[action.id] };
    }
    default:
      return { ...state };
  }
};

export const setFaqAC = id => ({ type: SET_FAQ, id });

export default faqPagereducer;