import ConsultPic1 from '../assets/images/consult/c1.jpg';
const SET_GETCONSULT = 'SET_GETCONSULT';
const initialState = {
  partOfState: {
    id: 'Consult',
    name: 'Consult',
    t1: '–û—Ç—Ä–∏–º–∞–π—Ç–µ –±–µ–∑–∫–æ—à—Ç–æ–≤–Ω—É –∫–æ–Ω—Å—É–ª—å—Ç–∞—Ü—ñ—é!',
    p1: '–í–∏ –º–æ–∂–µ—Ç–µ –æ—Ç—Ä–∏–º–∞—Ç–∏ –±–µ–∑–∫–æ—à—Ç–æ–≤–Ω—É –∞–Ω–æ–Ω—ñ–º–Ω—É –∫–æ–Ω—Å—É–ª—å—Ç–∞—Ü—ñ—é –∑ –ø–∏—Ç–∞–Ω—å, –ø–æ–≤‚Äô—è–∑–∞–Ω–∏—Ö —ñ–∑ –≤–∂–∏–≤–∞–Ω–Ω—è–º –Ω–∞—Ä–∫–æ—Ç–∏–∫—ñ–≤. –ó–∞—Ç–µ–ª–µ—Ñ–æ–Ω—É–π—Ç–µ –∑–∞ –Ω–æ–º–µ—Ä–æ–º (057)714-31-77 –∞–±–æ –Ω–∞–ø–∏—à—ñ—Ç—å –ª–∏—Å—Ç–∞ –Ω–∞ –∞–¥—Ä–µ—Å—É fondnn.ukr@gmail.com',
    pic1: ConsultPic1,
  }
};

const getConsultReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_GETCONSULT: {
      return { visibility: true, partOfState: initialState[action.id] };


    }
    default:
      return { ...state };
  }
};

export const setConsultAC = id => ({ type: SET_GETCONSULT, id });

export default getConsultReducer;